    return (
        <div className="countdown">
            <div className="hours value">01h</div>
            <div className="minutes value">02m</div>
            <div className="seconds value">03s</div>
            <div className="text" dangerouslySetInnerHTML={{__html: strings.countdownText}}></div>
        </div>
    )
