import React from 'react';
import './Carousel.css';

const strings = require('../../assets/strings.json');

export const Carousel = props => {
    return (
        <div className="carousel">
            <a target="_blank" rel="noopener noreferrer" className="currentImage" href={strings.currentImage.link}>
                <img src={strings.currentImage.image} alt="main"/>
            </a>
            <img className="prevImage" src={strings.prevImage} alt="previous"/>
            <img className="nextImage" src={strings.nextImage} alt="next"/>
        </div>
    )
}
