import React from 'react';
import './SocialLinks.css';

const strings = require('../../assets/strings.json');

export const SocialLinks = props => {
    const socialLinksArray = strings.socialLinks;
    return(
        
        <div className="socialLinks">
            {socialLinksArray.map((link) => {
                return(
                    <a key={link.link} target="_blank" rel="noopener noreferrer" href={link.link}>
                        <img className="svgLogo" src={require(`../../assets/socialIcons/${link.image}`)} alt={link.name}/>
                    </a>
                )
            })}
        </div>
    )
}
